/* =================================
   Estilos Generales
   ================================= */
   html {
    height: 100%;
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}
body {
    background-color: #f6f6f6;
    font-family: sans-serif;
    font-size: 15px;
    line-height: 1.6;
    color: #333;
    overflow-x: hidden;
}
a {
    color: #555;
    text-decoration: none;
}
.clearfix::before, .clearfix::after {
    content: "";
    display: table;
    clear: both;
}
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

/* =================================
   Encabezado
   ================================= */
.container > header {
    padding: 20px 0;
    text-align: center;
    margin-bottom: 30px;
}
.container > header h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #333;
}

/* =================================
   Contenido Principal
   ================================= */
.main {
    position: relative;
    padding-bottom: 60px;
}
.main > p {
    font-size: 1.1em;
    color: #777;
    text-align: center;
}

/* =================================
   Lista de Libros
   ================================= */
#bk-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
#bk-list li {
    margin: 20px;
    width: 200px;
    height: 300px;
    perspective: 1500px;
}

/* =================================
   Libro Individual
   ================================= */
.bk-book {
    position: relative;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: transform 0.5s ease-in-out;
    cursor: grab;
}
.bk-book:hover {
    transform: rotateY(20deg);
}
.bk-front,
.bk-back,
.bk-left,
.bk-right,
.bk-top,
.bk-bottom {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 5px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.15);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-align: center;
    background-color: #ddd;
}
.bk-front {
    transform: translateZ(15px);
    background-color: #eee;
    color: #333;
}
.bk-back {
    transform: rotateY(180deg) translateZ(15px);
    background-color: #ccc;
    color: #333;
    padding: 15px;
    font-size: 0.9em;
}
.bk-left {
    width: 30px;
    left: 0;
    transform-origin: left center;
    transform: rotateY(-90deg);
    background-color: #eee;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    filter: blur(3px);
}
.bk-right {
    width: 30px;
    right: 0;
    transform-origin: right center;
    transform: rotateY(90deg);
    background-color: #eee;
}
.bk-top {
    height: 20px;
    top: 0;
    transform-origin: top center;
    transform: rotateX(90deg);
    background-color: #eee;
}
.bk-bottom {
    height: 20px;
    bottom: 0;
    transform-origin: bottom center;
    transform: rotateX(-90deg);
    background-color: #eee;
}
.bk-cover {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    overflow: hidden;
    background-color: grey;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.bk-cover-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateZ(-1px);
}
.bk-left h2 {
    transform: rotate(-90deg);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: absolute;
    width: 280px;
    top: 50%;
    left: -125px;
    transform-origin: 0 0;
    text-align: center;
    font-size: 0.9em;
    color: #333;
}
.bk-left h2 span {
    display: block;
    line-height: 1.2;
}
.bk-info {
    position: absolute;
    bottom: -140px;
    left: 0;
    width: 100%;
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
    transform: translateY(20px);
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
#bk-list li:hover .bk-info {
    opacity: 1;
    transform: translateY(0);
}
.bk-info h3 {
    font-size: 1em;
    margin-top: 5px;
    color: #333;
}
.bk-info h3 span {
    display: block;
    font-size: 0.8em;
    color: #777;
}
.bk-info p {
    font-size: 0.85em;
    color: #555;
    margin-bottom: 10px;
}
.bk-remove-button {
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 8px 15px;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color 0.2s ease-in-out;
}
.bk-remove-button:hover {
    background-color: #d32f2f;
}

/* Estilos Responsivos */
@media (max-width: 576px) {
    #bk-list {
        justify-content: flex-start;
    }
    #bk-list li {
        width: 100%;
        height: auto;
        margin: 10px 0;
        perspective: none;
    }
    .bk-book {
        transform: none !important;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        height: auto;
    }
    .bk-front,
    .bk-back,
    .bk-left,
    .bk-right,
    .bk-top,
    .bk-bottom {
        position: relative;
        transform: none !important;
        backface-visibility: visible;
        box-shadow: none;
        border-radius: 0;
        flex-basis: auto;
    }
    .bk-cover {
        width: 100px;
        height: 150px;
        margin-right: 15px;
        background-size: cover;
    }
    .bk-left,
    .bk-right,
    .bk-back {
        display: none;
    }
    .bk-info {
        position: relative;
        opacity: 1;
        transform: translateY(0);
        text-align: left;
        flex-grow: 1;
        background-color: transparent;
        box-shadow: none;
        padding: 0;
    }
}
